document.addEventListener('DOMContentLoaded', function () {

    if ($(window).width() < 1100) {
        $('.lang').on('click', function () {
            $('.lang').toggleClass('active');
            $('.lang__list').slideToggle(200);
        });

        $('.header__chat').on('click', function () {
            $('.new-messages').toggleClass('active');
        });
    }

    $(document).mouseup(function (e) {
        if (!$('.header__chat').is(e.target)
            && $('.header__chat').has(e.target).length === 0) {
            $('.new-messages').removeClass('active');
        }
    });

    $('.page-nav-item__title').on('click', function () {

        if ($(this).parent().hasClass('active')) {
            $(this).next().slideUp(100);
            $(this).parent().removeClass('active');
        } else {
            $('.page-nav-item').removeClass('active');
            $('.page-nav-item .page-nav-item__list').slideUp(100);
            $(this).next().slideDown(100);
            $(this).parent().addClass('active');
        }
    });


    $('.burger').on('click', function () {
        $('.burger, .page-nav').toggleClass('active');
    });

    $('.faq-item .faq-item__title').on('click', function () {
        $('.faq-item .faq-item__descr').not($(this).next()).slideUp(300);
        $(this).next().slideToggle(300);
        $(this).toggleClass('active');
        $(this).parents('.faq-item').siblings().find('div').removeClass('active');
    });



    function tabs(tabTrigger, tabTriggerWrap, tabContent) {
        const tabBox = document.querySelectorAll(tabContent);
        const tabTop = document.querySelector(tabTriggerWrap);
        const tabBtn = document.querySelectorAll(tabTrigger);

        if (tabTop) {
            tabTop.addEventListener('click', (event) => {

                let tabClass = event.target.getAttribute("data-tab");

                if (tabClass) {

                    tabBtn.forEach(item => {
                        item.classList.remove('active');
                    });

                    event.target.classList.add('active');

                    tabBox.forEach(elem => {
                        elem.classList.remove('hide');
                        if (!elem.classList.contains(tabClass) && tabClass !== 'all') {
                            elem.classList.add('hide');
                        }
                    });
                }
            });
        }
    }

    tabs('.tabs__btn', '.tabs', '.tabs-content');


    function showTab(trigger, tab, content) {
        $(trigger).on('click', function () {
            $('.tabs__btn').removeClass('active');
            $(tab).addClass('active');
            $('.tabs-content').addClass('hide');
            $(content).removeClass('hide');
        });
    }

    showTab('.show-chat', '.tab-chat', '.chat');
    showTab('.show-file', '.tab-file', '.file');


    if ($('.tippy').length != 0) {
        tippy('.tippy', {
            duration: 300,
            zIndex: 9999999999,
            appendTo: 'parent',
        });

    }

    $('.js-example-basic-single').select2();
    $("input[type='tel']").mask("+38 (099) 999-99-99");

    $('#select-modal').select2({
        dropdownParent: $('.modal-profile')
    });

    for (let e of document.querySelectorAll('input[type="range"].slider-progress')) {
        e.style.setProperty('--value', e.value);
        e.style.setProperty('--min', e.min == '' ? '0' : e.min);
        e.style.setProperty('--max', e.max == '' ? '100' : e.max);
        e.addEventListener('input', () => e.style.setProperty('--value', e.value));
    }



    $('.slider-progress').on('input', function () {
        let thisVal = $(this).val();

        $('.procent__val').text(thisVal);
    });


    $('.quest-tippy').on('click', function (e) {
        e.preventDefault();
    });

    $('.originality-robots__radio').on('click', function (e) {
        e.stopPragation();
        if (e.target !== $('.quest-tippy')) {
            console.log(e.target);

        }
    });

    $('.originality-robots__radio input').on('click', function () {

        let btn = $('.form__btn');

        if (!$(this).is(':checked')) {
            $('.originality-robots__range').removeClass('hide');
            $('.originality-robots__procent').removeClass('hide');
        } else {
            $('.originality-robots__range').addClass('hide');
            $('.originality-robots__procent').addClass('hide');
        }
    });








});